
/* Global styles */
body {
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
}

/* Login Page Styles */
.login-container {
    display: flex;
    justify-content: flex-end; /* Aligns to the right */
    align-items: center;
    height: 100vh;
    /* background: url('../assets/icons/login-frame.svg') no-repeat;
    background-position: -292px -247px; /* Adjusted position according to the SVG */
    /* background-size: cover; Ensures the SVG covers the container */ 
}

.background-image-left-top-corner {
    background-image: url('../assets/icons/left-top-corner.svg');
    background-repeat: no-repeat;
    position: fixed;
    /* bottom: -40%;
    right: -68%; */
    width: 100%;
    height: 100%;
    z-index: -1;
}

.login-box {
    background-color: rgba(255, 255, 255, 0.9);
    width: 45%;
    padding: 40px;
    margin: 10%;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.login-title {
    font-size: 28px;
    margin-bottom: 20px;
    color: #333;
}

input::placeholder {
    text-align: right;
}

.input-group {
    margin-bottom: 15px;
    position: relative;
}

.input-group input {
    width: 90%;
    padding: 10px;
    padding-right: 40px;
    border-radius: 5px;
    border: 1px solid #ccc;
    text-align: right;
}

.input-group .icon {
	position: absolute;
	/* right: 10px; */
	margin-left: 10px;
	top: 50%;
	transform: translateY(-50%);
	color: #888;
}

.remember-me {
    display: flex;
    align-items: center;
    justify-content: flex-start; /* Changed to flex-start to move checkbox to the left */
    margin-bottom: 1rem;
    direction: rtl;
}

.remember-me label input {
    margin-left: 10px; /* Added spacing between checkbox and text */
}

.btn-primary {
    background-color: #28a745;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
}

.btn-primary:hover {
    background-color: #218838;
}

.create-account {
    margin-top: 20px;
    font-size: 14px;
}

/* Home Page Styles */
.header {
    background-color: #004d00;
    padding: 20px;
    text-align: center;
    color: white;
}

.header h1 {
    font-size: 36px;
    margin-bottom: 10px;
}

.search-bar {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.search-bar input {
    padding: 10px;
    width: 300px;
    border-radius: 5px 0 0 5px;
    border: none;
    text-align: right;
}

.search-button {
    background-color: #28a745;
    border: none;
    padding: 10px 20px;
    border-radius: 0 5px 5px 0;
    color: white;
    cursor: pointer;
}

.search-button:hover {
    background-color: #218838;
}

/* Fatwa actions Section Styles */
.fatwa-actions {
    display: flex;
    justify-content: center; /* Centers the entire section horizontally */
    gap: 15px; /* Adds spacing between the buttons */
    margin-top: 30px;
    width: 100%; /* Ensures the section takes the full width of the container */
    flex-wrap: wrap; /* Allows wrapping if necessary */
}

.fatwa-action {
    width: 50%;
    max-width: 300px;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: medium;
    display: flex;
    justify-content: center; /* Centers content horizontally */
    align-items: center; /* Centers content vertically */
    position: relative; /* Allows dropdown positioning */
}

.fatwa-action button {
    background: none; /* Remove background from button */
    border: none; /* Remove border from button */
    color: inherit; /* Inherit color from parent */
    padding: 0; /* Remove default padding */
}

/* Categories section */
.categories-dropdown {
    width: 100%;
    top: 100%;
    left: 0;
    z-index: 10;
    text-align: center;
    background-color: #eee;
}

.categories-dropdown button {
    width: 90%;
    text-align: center;
    padding: 10px 20px;
    margin: 5px auto;
    background-color: white; /* Background color for buttons */
    border-radius: 5px; /* Round the corners of the button */
    border: none; /* Remove the border */
    color: black;
    cursor: pointer; /* Add pointer cursor */
}


/* Recent Fatwas Section Styles */
.recent-fatwas {
    margin: 40px 20px 20px 20px;
    /* width: 69%; */
}

.fatwa-item {
    background-color: white;
    width: 80%;
    padding: 10px 20px;
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
    margin-bottom: 20px;
    direction: rtl;
}
  
.fatwa-item p {
    margin: 5px 0;
}
  
.fatwa-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}
  
.fatwa-title {
    margin-left: auto; /* Ensures the title stays on the right */
}
  
.fatwa-info {
    text-align: left;
}
  
.btn-link {
    background: none;
    color: #28a745;
    border: none;
    cursor: pointer;
    text-decoration: underline;
}

/* Nav Bar Styles */
.navbar {
    padding: 20px 20px;
    position: sticky;
    top: 0;
    z-index: 1000;
}

.navbar-container {
    display: flex;
    justify-content: space-between; /* Adjusted for better layout */
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

.logo img {
    height: 40px;
}

.logo a {
    color: #fff;
    text-decoration: none;
}

.navbar-menu {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
}

.navbar-item {
    margin: 0 20px;
    padding-bottom: 10px;
}

.navbar-item .navbar-link {
    color: #fff;
    text-decoration: none;
    padding: 10px 0;
    font-size: 16px;
    border-bottom: 2px solid rgba(255, 255, 255, 0.7);
    transition: all 0.3s ease;
}

.navbar-link-active-link {
    color: #012901;
    border-bottom: 3px solid #012901 !important;
}

.navbar-item .navbar-link:hover {
    color: #012901;
    border-bottom: 2px solid #012901;
}

.navbar-item .navbar-link::after {
    content: '';
    display: block;
    width: 100%;
    border-bottom: 2px solid transparent;
    transition: all 0.3s ease;
    transform: translateY(5px); /* Adds space between text and underline */
}

.navbar-item .navbar-link:hover::after {
    border-color: #012901;
}

.navbar-user {
    display: flex;
    align-items: center;
}

.navbar-user .navbar-link {
    color: #fff;
    margin-left: 20px;
    text-decoration: none;
    font-size: 16px;
    display: flex;
    align-items: center;
    transition: color 0.3s ease;
}

.navbar-user .navbar-link:hover {
    color: #012901; /* Darker shade of green */
}

.navbar-user i {
    margin-right: 8px; /* Space between icon and text */
    font-size: 18px; /* Icon size */
    transition: color 0.3s ease;
}

.navbar-user .navbar-link:hover i {
    color: #012901; /* Icon color change on hover */
}

.navbar-user-navbar-btn {
    padding: 8px 16px;
    font-size: 16px;
    border-radius: 20px;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.navbar-user-navbar-btn-active {
    background-color: #046504; /* Darker background color */
    color: #fff;
}

.navbar-user-navbar-btn-inactive {
    color: #fff;
}

.navbar-user-navbar-btn-inactive:hover {
    background-color: #3e8e41;
    color: #fff;
}

.navbar-user-navbar-btn-active:hover {
    background-color: #012901; /* Darker on hover */
    color: #fff;
}

/* Notifications and Profile Dropdowns */
.notifications-wrapper, .profile-wrapper {
    position: relative;
}

.icon-with-badge {
    position: relative;
    display: inline-block;
    margin-right: 8px;
}

.notifications-badge {
    position: absolute;
    top: -5px;
    right: -5px;
    background-color: red;
    color: white;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    font-size: 0.75rem;
    text-align: center;
    line-height: 15px;
}

.notifications-button {
    background: none;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.notifications-dropdown {
    position: absolute;
    top: calc(100% + 10px); /* Keep the original top margin */
    background: rgba(238, 238, 238, 0.9);
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    right: 0;
    width: 250px;
    display: none; /* Hide dropdown by default */
}

/* Add an invisible buffer area */
.notifications-wrapper::before, .profile-wrapper::before {
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    height: 10px;
    background: transparent;
    z-index: 999;
}

.notifications-wrapper:hover .notifications-dropdown,
.profile-wrapper:hover .profile-dropdown,
.notifications-wrapper:hover::before,
.profile-wrapper:hover::before {
    display: block; /* Show dropdown and buffer area on hover */
}

.notifications-dropdown-item {
    display: flex;
    align-items: center;
    text-align: right;
    direction: rtl;
    padding: 8px 12px;
    border-bottom: 1px solid #ccc;
    font-size: 0.75rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #333;
    text-decoration: none;
}

.notification-status {
    margin-left: 8px;
    font-size: 1rem;
    color: red; /* Default for unread notifications */
}

.notifications-dropdown-item.read .notification-status {
    color: green; /* Change color for read notifications */
}

.notifications-dropdown-item:last-child {
    border-bottom: none;
}

.notifications-dropdown-item:hover {
    background-color: #f5f5f5;
}

.profile-dropdown {
    position: absolute;
    top: calc(100% + 10px); /* Keep the original top margin */
    background: rgba(238, 238, 238, 0.9);
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    right: 0;
    width: 180px;
    display: none; /* Hide dropdown by default */
}

.profile-dropdown-item {
    display: block; /* Ensure items are block-level */
    width: 100%; /* Ensure items take full width of the container */
    padding: 8px 12px;
    text-align: right;
    direction: rtl;
    border-bottom: 1px solid #ccc;
    font-size: 0.9rem;
    color: #333;
    text-decoration: none;
    background-color: transparent; /* Ensure background color is transparent by default */
    transition: background-color 0.3s ease;
}

.profile-dropdown-item:last-child {
    border-bottom: none;
}

.profile-dropdown-item:hover {
    background-color: #f5f5f5; /* Background color change on hover */
}

.profile-dropdown-item button {
    all: unset; /* Remove default button styling */
    display: block; /* Make button a block-level element */
    width: 100%; /* Ensure button takes full width */
    padding: 8px 12px;
    text-align: right;
    direction: rtl;
    font-size: 0.9rem;
    color: #333;
    cursor: pointer;
    background-color: transparent; /* Ensure transparent background */
    transition: background-color 0.3s ease;
}

.profile-dropdown-item button:hover {
    background-color: #f5f5f5; /* Background color change on hover */
}

.profile-button i,
.profile-dropdown-item i {
    margin-left: 8px; /* Space between icon and text */
    font-size: 16px; /* Adjust icon size as needed */
}


