/* Chat widget styling */
#chat-widget-container {
    position: fixed;
    bottom: 20px;
    right: 20px;
    display: flex;
    flex-direction: column;
    z-index: 1000; /* Ensure it's on top of other content */
}

#chat-popup {
    height: 70vh;
    max-height: 70vh;
    transition: all 0.3s ease;
    overflow: hidden;
    background-color: white;
}

@media (max-width: 768px) {
    #chat-popup {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        max-height: 100%;
        border-radius: 0;
    }
}

input#chat-input::placeholder {
    direction: rtl; /* Align placeholder text to the right */
}

